<template>
  <div>
    <div class="clearFix">
      <div class="project-tip">
        我的团队
      </div>
      <template v-if="!my">
        <div class="card-box" @click="addTeam = true">
          <Card dis-hover class="card">
            <Spin size="large" fix></Spin>
          </Card>
        </div>
      </template>
      <div class="card-box" v-if="my" v-for="team of my" @click="goTeam(team)">
        <Card dis-hover class="card">
          <img :src="baseUrl + team.logoUrl"
               style="position: absolute; top: 0; left: 0; width: 100%; height: 100%;">
          <div style="position: absolute; top: 0; left: 0; width: 100%; height: 100%;">
            <div style="text-align:center;">
              <h3>{{team.name}}</h3>
            </div>
          </div>
          <span href="#" slot="extra" @click.stop="editTeam(team)" class="hover">
            <Icon type="edit" class="edit"></Icon>
          </span>
        </Card>
      </div>
      <div class="card-box" @click="addTeam = true">
        <Card dis-hover class="card">
          <Icon class="plus" type="plus-round"></Icon>
        </Card>
      </div>
    </div>
    <div class="clearFix">
      <div class="project-tip">
        我参与的团队
      </div>
      <div class="card-box" v-for="team of join" @click="goTeam(team)">
        <Card dis-hover class="card">
          <img :src="baseUrl + team.logoUrl"
               style="position: absolute; top: 0; left: 0; width: 100%; height: 100%;">
          <div style="position: absolute; top: 0; left: 0; width: 100%; height: 100%;">
            <div style="text-align:center;">
              <h3>{{team.name}}</h3>
            </div>
          </div>
        </Card>
      </div>
    </div>
    <Modal
      :title="formTeam.id ? '修改团队' : '创建团队'"
      @on-ok="subTeam"
      @on-cancel="clearForm"
      v-model="addTeam"
      :loading="addLoading"
      class-name="vertical-center-modal">
      <Form ref="formTeam" :model="formTeam" :rules="ruleTeam">
        <div class="ivu-upload" style="width: 250px; height: 100px; margin-bottom: 10px; position: relative;">
          <input type="file" @change="changeImg" class="ivu-upload-input-b">
          <img v-if="formTeam.logoUrl" style="width: 250px; height: 100px;" :src="baseUrl + formTeam.logoUrl">
          <div class="ivu-upload ivu-upload-drag" v-if="!formTeam.logoUrl">
            <div style="padding: 10px 0px;">
              <i class="ivu-icon ivu-icon-ios-cloud-upload"
                 style="font-size: 52px; color: rgb(51, 153, 255);"></i>
              <p>点击上传团队头像</p>
            </div>
          </div>
        </div>
        <Form-item prop="name">
          <Input v-model="formTeam.name" placeholder="团队名称">
          </Input>
        </Form-item>
        <Form-item>
          <Input v-model="formTeam.remark" type="textarea" placeholder="备注">
          </Input>
        </Form-item>
      </Form>
    </Modal>
  </div>
</template>
<script>
  import getData from '../config/getData'
  import baseUrl from '../config/baseUrl'
  export default {
    created () {
      this.searchTeam()
    },
    data () {
      return {
        baseUrl,
        addTeam: false,
        my: null,
        join: null,
        addLoading: true,
        formTeam: {
          id: '',
          name: '',
          logoUrl: '',
          remark: ''
        },
        ruleTeam: {
          name: [
            { required: true, message: '请填写团队名称', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      changeImg (event) {
        let file = event.target.files[0]
        if (file) {
          let formData = new window.FormData()
          let type = file.name.split('.')
          type = type[type.length - 1]
          formData.append('image', file, file.name)
          formData.append('format', type)
          formData.append('bus', 3)
          getData('fileUpload', formData).then(res => {
            this.formTeam.logoUrl = res.data.data.url
          })
        }
      },
      goTeam (team) {
        this.$router.push({
          path: '/app/teamInfo/' + team.id
        })
      },
      editTeam (team) {
        this.addTeam = true
        this.formTeam.id = team.id
        this.formTeam.name = team.name
        this.formTeam.logoUrl = team.logoUrl
        this.formTeam.remark = team.remark
      },
      subTeam () {
        this.$refs.formTeam.validate((valid) => {
          if (valid) {
            getData(this.formTeam.id ? 'teamUpdate' : 'teamAdd', {
              ...this.formTeam
            }).then(() => {
              this.$Message.success(this.formTeam.id ? '修改成功' : '创建成功')
              this.clearForm()
              this.addTeam = false
              this.searchTeam()
            }, () => {
              this.$Message.error(this.formTeam.id ? '修改失败' : '创建失败')
              this.addLoading = false
              setTimeout(() => {
                this.addLoading = true
              }, 10)
            })
          } else {
            this.addLoading = false
            setTimeout(() => {
              this.addLoading = true
            }, 10)
          }
        })
      },
      clearForm () {
        this.formTeam.id = ''
        this.formTeam.name = ''
        this.formTeam.remark = ''
        this.formTeam.logoUrl = ''
      },
      searchTeam () {
        getData('teamMySearch').then(res => {
          this.my = res.data.data
        }, res => {
        })
        getData('teamJoinSearch').then(res => {
          this.join = res.data.data
        }, res => {
        })
      }
    }
  }
</script>
<style lang="scss" type="text/scss">
  .project-tip {
    margin: 10px 16px 0;
    color: #39f;
    font-size: 16px;
  }
</style>



